.gif-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  width: 300px;
  margin-bottom: 20px;
}

body {
  background-image: url("https://i.pinimg.com/originals/98/29/21/9829215db6f9210c0ae4e318e854cb1f.png");
  /* background-image: url("https://www.xtrafondos.com/wallpapers/vertical/anatomia-de-rick-y-morty-9241.jpg"); */
  /* background-image: url("https://www.xtrafondos.com/wallpapers/rick-y-morty-escapando-de-portal-9235.jpg"); */
  background-size: contain;
  background-repeat: repeat;
  background-position: center center;
  background-attachment: fixed;
}

.card-name:hover {
  background-color: lightblue;
  cursor: pointer;
  transform: scale(1.05);
  transition: transform 0.2s;
}

.containerButtonUp {
  display: flex;
  justify-content: center;
  text-align: center;
}

.containerTitle {
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: rgba(
    47,
    251,
    115,
    0.756
  ); /* En el cuarto parametro metemos la opacidad -> opacity: .5; De esta forma no le afecta la opacidad al texto dentro del div contenedor que queremos que sea medio transparente*/
}

.gif {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.loader-container {
  height: 100px;
  width: 100px;
}

.navbar-img {
  height: 60px;
  width: 60px;
  padding: 10px;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar {
  background-color: #75cbed;
}

.character-img:hover {
  cursor: pointer;
  transform: scale(1.06);
  transition: transform 0.2s;
}

.imgNotFound {
  margin: 15px 0 15px 0;
}

.navbar-title {
  font-family: 'get_schwifty';
  font-size: x-large;
}

@font-face {
  font-family: 'get_schwifty';
  src: url('./fonts/get_schwifty.ttf');
}